<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <tg-list :IsShowAction="false" ModuleName="Ticketing" SubModuleCode="ManageCategory"
            :RenderRowActionMethod="renderActions" v-on:ActionButtonClick="actionButtonClick" IdentityColumn="TICKET_ARTICLE_CATEGORY_ID"
            :HeaderText="$t('ManageCategory')" :ListData="SAData" :HeaderData="Headers"
            :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords" :SearchFields="leftSearchFields"
            :ListDataCallBackFunction="FetchData" :showCheckBox="true" :SortExp="SortExp" :SortBy="SortBy"
            :NorecordfoundText="$t('NoRecordfound')" v-on:LeftsearchButtonClick="leftCommonSearch"
            v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick"
            v-on:SortdataButtonClick="sortdata">    
            <template v-slot:slotdata="{data}">  
                <template v-if="data.column.COLUMN_NAME == 'NoOfArticle'">
                    <td class="text-center single-action">
                        <span><em class="fa fa-file-text-o" aria-hidden="true"></em><span class="noti-circle noti-green">{{ data.row.NoOfArticle }}</span></span>
                    </td>                   
                </template>
                <template v-if="data.column.COLUMN_NAME == 'STATUS_NAME'">
                    <a class="inactive-disabled">                      
                        <span v-bind:class="getStatusClassList(data.row.STATUS_NAME, 'STATUS_NAME')">
                            {{ data.row[data.column.COLUMN_NAME] }}
                        </span>
                    </a>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'Edit'">
                    <td class="text-center single-action">
                        <a  @click="AddCategory(data.row.TICKET_ARTICLE_CATEGORY_ID)"  title="Edit" class="updateCat editicon ">
                            <em class="fa fa-pencil text-success action-icon"></em>
                        </a>
                    </td>                   
                </template>
            </template>        
        </tg-list> 
        <AddCategory v-if="IsCategory" :CategoryId="CategoryId"></AddCategory>       
    </section>
</template>
<script>
import Schema from './ManageCategorySchema'
import DataServices from "@/services/DataService";
import AddCategory from './AddCatgory.vue'
export default {   
    components:{AddCategory},
    data() {
        return {
            isLoading: false,
            CategoryId:null,
            IsAddCannedReply: false,
            listheaderbuttons: Schema.buttons,
            leftSearchFields: Schema.leftsearchSchema,                       
            Headers: [{
                "COLUMN_NAME": "TICKET_ARTICLE_CATEGORY_NAME",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Category Name",
                "DISPLAY_ORDER": 1 
                }, 
                {
                "COLUMN_NAME": "SUBCATEGORYNAME",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Sub Category",
                "DISPLAY_ORDER": 2,
                 },
                 {
                "COLUMN_NAME": "STATUS_NAME",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Status",
                "DISPLAY_ORDER": 3,
                settings : {
                    isInSlot : true
                 }
                 }, 
                 {
                "COLUMN_NAME": "NoOfArticle",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Number Of Articles",
                "DISPLAY_ORDER": 4,
                settings : {
                    isInSlot : true
                 }
                 },
                 {
                "COLUMN_NAME": "Edit",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Edit",
                "DISPLAY_ORDER": 5,
                settings : {
                    isInSlot : true
                 }
                 }
            ],
            SAData: [],
            SortBy: '',
            SortExp: '',
            TotalRecords: 0,
            PageNumber:1,
            PageSize:10,
            searchCondition:'',
            TemplateId:null,
            IsCategory:false
        }
    },
    created:async function () {
        await Schema.bindleftCommonSearchdropdown(this, "Ticketing", "Category");
        this.FetchData();
    },
    methods:{
        getStatusClassList: function (status) {
            var returnClass = '';
            switch (status) {
                case 'Active':
                    returnClass = 'px-2 text-center bg-success text-white d-block';
                    break;
                case 'Inactive':
                    returnClass = 'px-2 text-center bg-danger text-white d-block';
                    break;
                default:
                    returnClass = '';
            }
            return returnClass;
        },
        FetchData:function(){ 
            var vm=this;
            vm.isLoading=true;
            var url = `sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&isPaged=&pageSize=${vm.PageSize}&pageNum=${vm.PageNumber}&${vm.searchCondition}`;
            DataServices.ManageCategoryList(url).then(response=>{
                if(response.data.status=='Success'){    
                    if(response.data.result.data.length>0){                        
                        vm.SAData=response.data.result.data;                                    
                        vm.TotalRecords = response.data.result.data[0].TOTAL_RECORDS;
                        vm.listheaderbuttons[1].isdisabled = true;  
                        vm.isLoading=false; 
                    }                  
                }
            })
        },
        commonHeaderButtonClick: function (event) {            
            switch (event.callbackfunction) {
                case 'callAdd':
                    this.AddCategory(0);
                    break;
                case "commonDeleteData":
                    this.commonDeleteData();
                    break;
            }
        },
        AddCategory:function(value){
            this.CategoryId=value;
            this.IsCategory=true;
        },
        renderActions: function () { },      
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        leftCommonSearch: function (value) {               
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchConditionJson != 'undefined') {
                this.searchCondition = value.searchConditionJson;
            }
            else {
                this.searchCondition = "";
            }
            this.FetchData();
        },
        actionButtonClick: function () { },
        sortdata: function (sortBy) {            
            this.SortBy = sortBy;
            var sortExp = 'ASC';
            if (this.SortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.SortExp = sortExp;
            this.SortBy = sortBy;
            this.SortExp = sortExp;
            this.PageNumber = 1;
            this.GroupStart = this.PageNumber;
            this.GroupEnd = this.PageNumber + 2;
            this.FetchData();
        },     
        commonDeleteData: function (event) {            
            var vm = this;
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
            if (val.length > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Category")), function () {
                    var objectPostString = `ids=${val}&tableName=${'TALYGEN_ticket_article_category'}&primaryKey=${'ticket_article_category_id'}`;   
                    DataServices.DeleteData(objectPostString).then(response => {
                        var box = "";
                        if (response.data == "DeletedSuccess") { 
                            vm.listheaderbuttons[1].isdisabled = true;                                                   
                            vm.FetchData();
                            box = vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Category')]), "Success", true, vm.$t('Alert'));
                        }
                        else {
                            box = vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Category')]), "failure", true, vm.$t('Alert'));
                        }
                        setTimeout(function () {
                            box.modal('hide');
                        }, 2000);
                    });
                });
            }
            else {
                alert(vm.$t("Selectanyrecordtodelete"));
            }
        },
        
    }    
}
</script>
<style>
.textstyle{
    color: #007bff;
    text-decoration: none;
    background-color: transparent;
}
</style>