import DataServices from "@/services/DataService";
let buttons =
    [
        {
            title: 'Add New',
            iconClass: 'fa fa-plus',        
            callbackfunction: 'callAdd',
            additionalClass: '',
            isdisabled: false,
            isvisible: true,
            checkPrivilege: true 
        },
        {
            id: 'DeleteMultiple',
            title: 'Delete',
            iconClass: 'fa-trash-o',
            callbackfunction: 'commonDeleteData',
            isdisabled: true,
            isvisible: true,
            isEnabledOnSelectedRow: true
        }
    ];

let leftsearchSchema = [
    {
        fieldName: 'Category',
        fieldType: "radio",
        fieldIcon: "fa fa-envelope",
        fieldSearchConditionName: [''],
        paramName: "categoryId",
        isSearch: false,
        value: "",
        listOptions: []
    },
    {
        fieldName: 'Sub Category',
        fieldType: "text",
        fieldIcon: "fa fa-envelope",
        fieldSearchConditionName: [''],
        paramName: "subCategoryId",
        isSearch: false,
        value: "",
        listOptions: []
    },
    {
        fieldName: 'Status',
        fieldType: "radio",
        fieldIcon: "fa fa-envelope",
        fieldSearchConditionName: [''],
        paramName: "statusId",
        isSearch: false,
        value: "",
        listOptions: [{name:'Active',value:1001},{name:'Inactive',value:1002}]
    },
];

async function bindleftCommonSearchdropdown(instance,modulename, filtername) { 
    var vm = this;   
    if(filtername=='Category'){        
        DataServices.CategoryListDDL().then(response => {        
            if (response.data.result.data.length > 0 && response.data.result.data !='') {
                var CategortData = response.data.result.data;
                CategortData.forEach(element => {                   
                    let obj = {
                        name: element.TICKET_ARTICLE_CATEGORY_NAME,
                        value: element.TICKET_ARTICLE_CATEGORY_ID
                    }                           
                    vm.leftsearchSchema[0].listOptions.push(obj);                                   
                });
            }
        })
    }
       
} 

export default { buttons, leftsearchSchema ,bindleftCommonSearchdropdown};