<template>
    
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
       <div class="modal d-block">
           <div class="modal-dialog modal-dialog-centered">
               <div class="modal-content">
                   <div class="modal-header theme-primary partition-full">
                       <h4 class="modal-title">{{ $t('AddCategory') }}</h4>
                       <button type="button" class="close" v-on:click="Close">
                           <em class="fa fa-times" aria-hidden="true"></em>
                       </button>
                   </div>
                   <div class="modal-body">
                       <dynamic-form ref="AddForm" lang="en" :buttons="buttons" :schema="FormSchema"
                           v-on:OnSubmit="onSubmit"></dynamic-form>
                   </div>
               </div>
           </div>
       </div>
   </div>

</template>
<script>
import DataService from '../../services/DataService';

export default{
    props:{
        CategoryId:{
            type:Number
        }
    },
    data(){
        return{
            isLoading:false,
            FormSchema: [
                {
                    layoutType: "double",                    
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('Category Name'),
                            name: "CategoryName",
                            value: "",
                            placeholder: "",
                            disabled: false,
                            config: {
                                options: []                               
                            },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Parent Category'),
                            name: "ParentCategoryName",
                            value: "",
                            placeholder: "",
                            disabled: false,
                            config: {
                                options: []                               
                            },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Status'),
                            name: "Status",
                            value: "",
                            placeholder: "",                          
                            config: {
                                options: []                              
                            },
                        }
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",                   
                    text: "<i class='fa fa-save pr-2'></i>Save",
                },               
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Close",
                    onClick: this.Close
                }
            ],
        }
    },
    created:function(){
        this.CategoryListDDL();
        if(this.CategoryId != null){
            this.GetDetails();
        }
    },
    methods:{
        Close:function(){
            this.$parent.IsCategory=false;
        },        
        CategoryListDDL:function(){
            DataService.CategoryListDDL().then(response=>{
                if(response.data.result.data.length>0){
                    response.data.result.data.forEach(element => {
                        let obj={ 
                            name:element.TICKET_ARTICLE_CATEGORY_NAME,
                            value:element.TICKET_ARTICLE_CATEGORY_ID
                        }
                        this.FormSchema[0].Data[1].config.options.push(obj);
                    });
                    this.FormSchema[0].Data[2].config.options=[{value:1001,name:'Active'},{value:1002,name:'Inactive'}];
                    this.$refs.AddForm.UpdateKeyValue();
                }
            });
        },
        onSubmit(formfields, val) {
            var vm = this;
            vm.isLoading=true;
            var jsonobj = {
                CategoryId:  vm.CategoryId==0?0:vm.CategoryId,
                RequestType: vm.CategoryId==0?'ADD':'UPDATE',
                StatusId: formfields.Status,
                ParentCategoryId: formfields.ParentCategoryName,
                CategoryName: formfields.CategoryName                
            };
            var params = JSON.stringify(jsonobj);
            DataService.SaveUpdateCategory(params).then(response=>{
                if(response.data.status=='Success'){
                    if(response.data.result==0){
                        vm.ShowAlert(vm.$t('AddedSuccess', [vm.$t('Category')]), "Success", true, vm.$t('Alert'));
                        vm.$parent.FetchData();
                        vm.Close();
                    }
                    else if(response.data.result==3){
                        vm.ShowAlert(vm.$t('AlreadyExists', [vm.$t('Category')]), "failure", true, vm.$t('Alert'));
                        vm.Close();
                    }
                    else if(response.data.result==1){
                        vm.ShowAlert(vm.$t('UpdatedSuccess', [vm.$t('Category')]), "Success", true, vm.$t('Alert'));
                        vm.$parent.FetchData();
                        vm.Close();
                    }
                }
                vm.isLoading=false;
            })
        },
        GetDetails:function(){
            var vm =this;
            var url=`categoryId=${vm.CategoryId}`
            DataService.GetDetails(url).then(response=>{                
                if(response.data.result.data.length>0){
                    var CategoryData=response.data.result.data;
                    vm.FormSchema[0].Data[0].value=CategoryData[0].ticket_article_category_name;
                    vm.FormSchema[0].Data[1].value=CategoryData[0].ticket_article_category_parent;
                    vm.FormSchema[0].Data[2].value=CategoryData[0].status_id;
                }
            });
            vm.$refs.AddForm.UpdateKeyValue();
        }
    }
}
</script>